
import { defineComponent, nextTick } from "vue"
import trapFocus from "./lib/directives/trapFocus"
import { removeElement } from "./lib/helpers"
import TwLink from "./tw-link.vue"
import TwSymbol from "./tw-symbol.vue"

export default defineComponent({
  name: "tw-modal",
  props: {
    modelValue: {
      type: [Boolean, Object],
      default: false,
    },
    title: {
      type: String,
      default: "",
    },
    close: {
      type: Boolean,
      default: true,
    },
    standalone: {
      type: Boolean,
      default: false,
    },
    role: {
      default: "dialog",
    },
    canCancel: {
      type: Boolean,
      default: true,
    },
    onCancel: {
      type: Function,
      default: () => {
        // log("onCancel not defined")
      },
    },
  },
  components: {
    TwSymbol,
    TwLink,
  },
  directives: {
    trapFocus,
  },
  emits: ["close", "update:modelValue", "didopen", "willclose", "cancel"],
  methods: {
    doCancel() {
      this.$emit("cancel")
      // let onCancel = this?.$parent?.onCancel || this?.onCancel
      // if (onCancel) {
      //   onCancel.apply(null, arguments)
      // }
      this.doClose()
    },
    doClose() {
      this.$emit("close", false)
      this.$emit("update:modelValue", false)

      if (this.standalone) {
        // Timeout for the animation complete before destroying
        setTimeout(() => {
          // this.value = false
          // @ts-ignore
          this.$destroy()
          removeElement(this.$el)
        }, 150)
      }
    },
    keyPress(event: KeyboardEvent) {
      if (this.modelValue && event.keyCode === 27) {
        // Esc key
        this.doCancel()
      }
    },
    doFocus() {
      nextTick(() => {
        let el = this.$el.querySelector(".focus")
        console.log("FOCUS", this.$el)
        if (el) {
          el.focus()
        }
      })
    },
  },
  created() {
    document?.addEventListener("keyup", this.keyPress)
  },
  watch: {
    async modelValue(value) {
      if (!value) {
        this.$emit("willclose")
      } else {
        this.$emit("didopen")
        this.doFocus()
      }
    },
  },
  // mounted() {
  //   this.$nextTick(() => {
  //     console.log("mounted", this.modalValue)
  //     if (this.modalValue) {
  //       this.doFocus()
  //     }
  //   })
  // },
  beforeMount() {
    // Insert the Dialog component in the element container
    if (this.standalone && typeof window !== "undefined") {
      nextTick(() => {
        const container =
          /* document.querySelector(this.container) || */ document.body
        container.appendChild(this.$el)
      })
    }
  },
  beforeUnmount() {
    if (typeof window !== "undefined") {
      document?.removeEventListener("keyup", this.keyPress)
      // reset scroll
      // document?.documentElement.classList.remove('is-clipped')
    }
  },
  // setup(props) {},
})
